import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, Checkbox, message } from 'antd';
import { getGradeParts, deleteGradePart } from 'services/typeComponentPoint';

class ListTypeComponentPointPage extends Component {
  state = {
    reload: 0,
  };

  handleDelete = async id => {
    await deleteGradePart(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const columns = [
      // {
      //   title: 'STT',
      //   dataIndex: 'stt',
      //   key: 'id',
      // },
      {
        title: 'Ký hiệu',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Tên',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Hệ số',
        dataIndex: 'multiplyBy',
        key: 'multiplyBy',
        align: 'center',
      },
      {
        title: 'Tỷ lệ nhóm',
        dataIndex: 'ratio',
        key: 'ratio',
        align: 'center',
      },
      {
        title: 'Nhóm thành phần',
        dataIndex: 'partGroup',
        key: 'partGroup',
        align: 'center',
      },
      {
        title: 'Chuyên cần',
        dataIndex: 'isAttendant',
        key: 'isAttendant',
        align: 'center',
        render: text => <Checkbox checked={text} />,
      },
      {
        title: 'Chọn mặc định',
        dataIndex: 'isDefault',
        key: 'isDefault',
        align: 'center',
        render: text => <Checkbox checked={text} />,
      },    
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        align: 'center',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#type-component-point/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Điểm thành phần">
        <CartHeadAction>
          <BtnAddNew href="#type-component-point/form/new" />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getGradeParts}
              columns={columns}
              // rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListTypeComponentPointPage />
  </Page>
);



